@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&family=Ubuntu:wght@400;500;700&display=swap);
/* Login Component */
.login .main-content,
.register .main-content{
    width: 100%;
    padding: 0px 5px;
    padding-top: 100px;
    padding-bottom: 40px;
    min-height: 93.5vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #000;
}
.login .center-div,
.register .center-div{
    max-width: 100%;
    padding: 5px 10px;
    border: 5px solid #fff;
    background-color: #000;
}
.login .center-div h1,
.register .center-div h1{
    font-size: 3.5rem;
    margin-bottom: 10px;
    text-align: center;
}
.login .center-div .form-container,
.register .center-div .form-container{
    width: 100%;
}
.login .center-div .form-container form,
.register .center-div .form-container form{
    display: flex;
    flex-direction: column;
}
.login .center-div .form-container form .input-div,
.register .center-div .form-container form .input-div{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin: 25px;
}
.login .center-div .form-container form .input-div input,
.register .center-div .form-container form .input-div input{
    background-color: transparent;
    border: none;
    outline: none;
    border-bottom: 2px solid #fff;
    font-size: 2rem;
    color: #fff;
}
.login .center-div .form-container form .submit-div,
.register .center-div .form-container form .submit-div{
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 15px 0px;
}
.login .center-div .form-container form .submit-div input,
.register .center-div .form-container form .submit-div input{
    background-color: transparent;
    font-size: 2rem;
    background-color: #fff;
    color: #000;
    cursor: pointer;
    margin-right: 10px;
    border-style: none;
    padding: 5px 20px;
}
.login .center-div .form-container form .submit-div a,
.register .center-div .form-container form .submit-div a{
    color: #fff;
    font-size: 2rem;
}


/* Responsiveness */
@media (max-width: 500px) {
    .login .main-content,
    .register .main-content{
        padding: 5px;
    }
}

@media (max-width: 400px){
    .login .center-div,
    .register .center-div{
        width: 100%;
        /* border: none; */
        
        /* border: 5px solid green; */
    }

    .login .center-div h1,
    .register .center-div h1{
        margin-bottom: none;
    }
}
