    justify-content: center;
    align-items: center;
}
.profile .container .main-content .top-div .basic-info   #save-icon{
    position: absolute;
    right: 0px;
    top: 0px;
    height: 70px;
    width: 70px; 
    color: #000;
    background-color: #000;
    cursor: pointer;
    border-radius: 50%;
}
.profile .container .main-content .top-div .basic-info   #save-icon:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    transition: 0.2s;
}
.profile .container .main-content .top-div .basic-info   .col-left .user-img-container{
    position: relative;
}
.profile .container .main-content .top-div .basic-info   .col-left .user-img-container>img{
    height: 200px;
    width: 100%;
    object-fit: cover;
}
.profile .container .main-content .top-div .basic-info   .col-left .user-img-container input{
    display: none;
}
.profile .container .main-content .top-div .basic-info   .col-left .user-img-container img{
    color: #000;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    border: 10px solid #000;
    object-fit: cover;
}
.profile .container .main-content .top-div .basic-info   .col-left .user-img-container button{
    position: absolute;
    left: 50%;
    top: 50%;
    background-color: transparent;
    border: none;
    outline: none;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: #fff;
    font-size: 2rem;
    font-weight: bold;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    cursor: pointer;
    display: none;
}
.profile .container .main-content .top-div .basic-info   .col-left .user-img-container:hover img{
    -webkit-filter: brightness(30%);
            filter: brightness(30%);
}
.profile .container .main-content .top-div .basic-info   .col-left .user-img-container:hover button,
.profile .container .main-content .top-div .basic-info   .col-left .user-img-container button:hover{
    display: inline;
}
.profile .container .main-content .top-div .basic-info   .col-right{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    flex: 1 1;
    padding: 0px 5px;
}
.profile .container .main-content .top-div .basic-info   .col-right input{
    font-size: 2rem;
    color: #000;
    text-align: center;
    font-weight: bold;
    padding: 0px 5px;
    border: 5px solid #000;
    border-radius: 10px;
    outline: none;
    margin: 30px 0px;
    box-shadow: 5px 5px 30px 2px #000;
}

/* Middle div */
.profile .container .main-content .middle-div{
    background-color: #fff;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
